<template>
    <a-modal
        :title="title"
        :width="800"
        :visible="visible"
        :confirmLoading="loading"
        @ok="
      () => {
        $emit('ok')
      }
    "
        @cancel="
      () => {
        $emit('cancel')
      }
    "
    >
        <a-spin :spinning="loading">
            <div class="plan-summary">
                <div class="plan-size-progress" v-if="plan !== null">
                    <a-row :gutter="24">
                        <a-col :span="8">
                            <a-statistic title="累计时长" :value="hourCount">
                                <template #suffix>h ({{durationYAxis.length}}天)</template>
                            </a-statistic>
                        </a-col>
                        <a-col :span="16">
                            <div class="ant-statistic">
                                <div class="ant-statistic-title">空间暂用</div>
                            </div>
                            <div class="ant-statistic-content">
                                <a-progress
                                    :percent="sizePercent"
                                    :format="sizePercentFormat"
                                    status="active"
                                />
                            </div>
                        </a-col>
                    </a-row>
                </div>
                <div id="plan-summary-size-chart"></div>
                <div id="plan-summary-duration-chart"></div>
            </div>
        </a-spin>
    </a-modal>
</template>

<script>
import { accAdd } from '@/libs/utils'
import { getSummaryList, getRecordPlan } from '@/api/record'
import * as echarts from 'echarts/core'
import { BarChart } from 'echarts/charts'
import {
    TitleComponent,
    TooltipComponent,
    GridComponent,
    DatasetComponent,
    TransformComponent,
    LegendComponent,
    ToolboxComponent,
    MarkLineComponent,
    MarkPointComponent
} from 'echarts/components'
import { LabelLayout, UniversalTransition } from 'echarts/features'
import { CanvasRenderer } from 'echarts/renderers'

echarts.use([
    MarkLineComponent,
    MarkPointComponent,
    ToolboxComponent,
    LegendComponent,
    TitleComponent,
    TooltipComponent,
    GridComponent,
    DatasetComponent,
    TransformComponent,
    BarChart,
    LabelLayout,
    UniversalTransition,
    CanvasRenderer
])
export default {
    props: {
        visible: {
            type: Boolean,
            default: true
        },
        loading: {
            type: Boolean,
            default: () => false
        }
    },
    components: {
        RecordPlan: () => import('@/components/RecordPlan')
    },
    beforeDestroy() {
        this.destroyChart()
    },
    data() {
        return {
            title: '录像统计',
            plan: null,
            summaryList: [],
            xAxis: [],
            sizeYAxis: [],
            durationYAxis: [],
            sizeChart: null,
            durationChart: null,
            sizePercentFormat: (percent, _successPercent) => {
                return percent + '%' + '(' + this.sizeCount + 'GB/' + this.plan.limit_space + 'GB)'
            }
        }
    },
    computed: {
        sizeCount() {
            let sizeCount = 0
            this.sizeYAxis.forEach((item) => {
                sizeCount = accAdd(sizeCount, Math.floor(parseFloat(item) * 100) / 100)
            })

            return sizeCount
        },
        hourCount () {
            let hours = 0
            this.durationYAxis.forEach((item) => {
                hours = accAdd(hours, parseFloat(item))
            })

            return hours
        },
        sizePercent() {
            if (this.sizeCount >= this.plan.limit_space) {
                return 100
            }

            return Math.floor(((this.sizeCount * 100) / this.plan.limit_space) * 100) / 100
        }
    },
    methods: {
        destroyChart() {
            if (this.sizeChart) {
                this.sizeChart.dispose()
            }
            if (this.durationChart) {
                this.durationChart.dispose()
            }
        },
        loadPlan(id) {
            getRecordPlan(id).then((response) => {
                const { plan } = response
                this.plan = plan
            })
        },
        loadSummaryList(id) {
            getSummaryList({ plan_id: id }).then((response) => {
                this.summaryList = response
                this.parseSummary()
                this.$nextTick(() => {
                    this.destroyChart()
                    this.renderChart()
                })
            })
        },
        initData(id) {
            this.loadPlan(id)
            this.loadSummaryList(id)
        },
        parseSummary() {
            // 可以换成计算属性
            let xAxis = []
            let sizeYAxis = []
            let durationYAxis = []
            this.summaryList.forEach((item) => {
                xAxis.push(item.record_date)
                sizeYAxis.push(item.sizes)
                durationYAxis.push(item.hours)
            })
            this.xAxis = xAxis
            this.sizeYAxis = sizeYAxis
            this.durationYAxis = durationYAxis
        },
        renderChart() {
            this.sizeChart = echarts.init(document.getElementById('plan-summary-size-chart'))
            this.durationChart = echarts.init(document.getElementById('plan-summary-duration-chart'))
            const sizeOption = {
                color: ['#c23531'],
                title: {
                    text: '空间大小统计',
                    subtext: '单位：GB'
                },
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'shadow'
                    }
                },
                legend: {},
                toolbox: {
                    show: true,
                    feature: {
                        dataView: { show: true, readOnly: false },
                        restore: { show: true },
                        saveAsImage: { show: true }
                    }
                },
                calculable: true,
                xAxis: {
                    type: 'category',
                    data: this.xAxis
                },
                yAxis: {
                    type: 'value'
                },
                series: [
                    {
                        emphasis: {
                            focus: 'series'
                        },
                        // markPoint: {
                        //     data: [
                        //         { type: 'max', name: 'Max' },
                        //         { type: 'min', name: 'Min' }
                        //     ],
                        //     itemStyle: {
                        //         normal: {
                        //             label: {
                        //                 show: false,
                        //                 formatter: '{c}GB'
                        //             }
                        //         }
                        //     }
                        // },
                        markLine: {
                            data: [{ type: 'average', name: 'Avg' }],
                            itemStyle: {
                                normal: {
                                    label: {
                                        show: true,
                                        formatter: '{c}GB'
                                    }
                                }
                            }
                        },
                        data: this.sizeYAxis,
                        type: 'bar',
                        itemStyle: {
                            normal: {
                                label: {
                                    show: true,
                                    formatter: '{c}GB'
                                }
                            }
                        }
                    }
                ]
            }
            this.sizeChart.setOption(sizeOption)
            const durationOption = {
                title: {
                    text: '录制时长统计',
                    subtext: '单位：小时'
                },
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'shadow'
                    }
                },
                toolbox: {
                    show: true,
                    feature: {
                        dataView: { show: true, readOnly: false },
                        restore: { show: true },
                        saveAsImage: { show: true }
                    }
                },
                xAxis: {
                    type: 'category',
                    splitLine: { show: false },
                    data: this.xAxis
                },
                yAxis: {
                    type: 'value'
                },
                series: [
                    {
                        // markPoint: {
                        //     data: [
                        //         { type: 'max', name: 'Max' },
                        //         { type: 'min', name: 'Min' }
                        //     ],
                        //     itemStyle: {
                        //         normal: {
                        //             label: {
                        //                 show: false,
                        //                 formatter: '{c}h'
                        //             }
                        //         }
                        //     }
                        // },
                        markLine: {
                            data: [{ type: 'average', name: 'Avg' }],
                            itemStyle: {
                                normal: {
                                    label: {
                                        show: true,
                                        formatter: '{c}h'
                                    }
                                }
                            }
                        },
                        data: this.durationYAxis,
                        type: 'bar',
                        itemStyle: {
                            normal: {
                                label: {
                                    show: true,
                                    formatter: '{c}h'
                                }
                            }
                        }
                    }
                ]
            }
            this.durationChart.setOption(durationOption)
        }
    }
}
</script>
<style lang="less" scoped>
.plan-summary {
    .plan-size-progress {
        width: 75%;
        padding: 10px;
        .plan-size-progress-label {
            font-size: 18px;
            font-weight: 700;
            color: #000000;
        }
    }
    #plan-summary-duration-chart {
        height: 300px;
    }
    #plan-summary-size-chart {
        height: 300px;
    }
}
</style>
